img 
    image-rendering: -webkit-optimize-contrast
.img-default
    width: auto 
    max-width: 100% 
    display: block
.a-default
    display: block
    outline: none
    color: inherit
    outline: none 
.btn-default
    border: none 
    background-color: rgba(white, 0)
    cursor: pointer
.btn-hover 
    transition: all 0.3s ease
    &:hover 
        opacity: 0.8
.a-hover 
    transition: opacity 0.3s ease 
    &:hover 
        opacity: 0.8
.d-block
    display: block
.d-inline 
    display: inline
.btn-default 
    outline: none
.wrap-img
    display: block
.cm-container
    max-width: 1120px 
    padding: 0px 20px 
    width: 100% 
    margin: auto
    